import "./AvailableMeals.css";
import Card from "../UI/Card";
import MealItem from "./MealItem/MealItem";

const DUMMY_MEALS = [
  {
    id: "m1",
    name: "Maki & Rolls Selection",
    description:
      "A selection of veggie and classic tempura prawn rolls alongside maki avocado and maki tuna fish.",
    price: 12.95,
  },
  {
    id: "m2",
    name: "Nigiri Salmon",
    description: "Signature rice layered with sumptuous fresh salmon fish.",
    price: 10.55,
  },
  {
    id: "m3",
    name: "Vegetarian Rice Bowl",
    description:
      "Ginger, fried shiitake mushrooms and sliced cucumber served with sushi rice.",
    price: 9.85,
  },
  {
    id: "m4",
    name: "Katsu Chicken Sando",
    description:
      "A Japanese style sandwich with katsu chicken, white cabbage and coriander.",
    price: 8.75,
  },
];

const AvailableMeals = () => {
  const mealsList = DUMMY_MEALS.map((meal) => (
    <MealItem
      id={meal.id}
      key={meal.id}
      name={meal.name}
      description={meal.description}
      price={meal.price}
    />
  ));

  return (
    <section className="meals">
      <Card>
        <ul>{mealsList}</ul>
      </Card>
    </section>
  );
};

export default AvailableMeals;
