import React from "react";
import "./MealsSummary.css";

const MealsSummary = (props) => {
  return (
    <section className="site-summary">
      <h2>Delivering Delicious To Your Door</h2>
      <p>
        Choose your favourite meal from our broad selection of popular local
        restaurants.
      </p>
      <p>Order now and get your food sorted in just a few clicks!</p>
    </section>
  );
};

export default MealsSummary;
